<script>
import {
    mapMutations
  } from 'vuex';

export default {
    name: 'NavBar',
    data: () => ({
      main_menu: [            
          {
          title: 'Home',
          link: '/',
          },
          {
          title: 'About',
          link: '/about',
          },
          
      ],
      drawer_menu: [            
          {
          title: 'Home',
          link: '/',
          },
          {
          title: 'About',
          link: '/about',
          },
          
      ],
      cartDrawer: false,
    }),
    
    computed: {
      homePage() {
        if(this.$route.path == "/" ) {
          return true
        } else {
          return false
        }
      },
      drawer: {
        get () {
          return this.$store.state.drawer
        },
        set (val) {
          this.setDrawer(val)
        },
      },
    },
    filters: {
      toCurrency(amount) {
        var formatter = new Intl.NumberFormat('en-US', {
          style: 'currency',
          currency: 'USD',
        });

        return formatter.format(amount)
      }
    },
    methods: {
      ...mapMutations(['toggleDrawer', 'setDrawer']),
      onClick (e, item) {
        e.stopPropagation()

        if (item.to === '/') {
          this.$vuetify.goTo(0)
          this.setDrawer(false)
          return
        }

        if (item.to || !item.href) return

        this.$vuetify.goTo(item.href)
        this.setDrawer(false)

        if (item.to || !item.href) return

        this.$vuetify.goTo(item.href.endsWith('!') ? 0 : item.href)
      }
    }
  }
</script>

